// === Mixins ===
@mixin font-lato {
    font-family: 'Lato', sans-serif; }
@mixin font-montserrat {
    font-family: 'Montserrat', sans-serif; }
// === / Mixins ===

// === Variables ===
$color-white: #ffffff;
// === / Variables ===

// === Common ===
body {
    @include font-lato; }

// === header ===
.section-header {
    color: #ffffff;
    &-logo {}
    &-content {}
    &-wrapper {
        &__title {}
        &__subtitle {}
        &__form {
            &-input {}
            &-button {} } } }
